
































import { Component, Vue } from 'vue-property-decorator';
import SearchBar from '../components/SearchBar.vue';
import List, { SelectedEvent } from '../components/List.vue';
import PackDialog from '../components/Dialog.vue';
import { IAuthors } from '@edave64/dddg-repo-filters/dist/authors';
import { IPack } from '@edave64/dddg-repo-filters/dist/pack';
import { Prop } from 'vue-property-decorator';

@Component({
	components: {
		SearchBar,
		List,
		PackDialog,
	},
})
export default class App extends Vue {
	@Prop() private authors!: IAuthors;
	@Prop() private packs!: IPack[];

	private search = '';
	private selected: string | null = null;

	private keydownHandler(event: KeyboardEvent) {
		if (event.key === 'Escape') {
			this.selected = '';
			this.$nextTick(() => {
				(this.$refs.searchBar as SearchBar).focus();
			});
		}
	}

	private onSelect({ id, source }: SelectedEvent) {
		this.selected = id;
		if (source === 'keyboard') {
			this.$nextTick(() => {
				const dialog = this.$refs.dialog as PackDialog;
				dialog.focus();
			});
		}
	}

	private focusListHandler() {
		(this.$refs.list as List).focus();
	}
}
